.menuArea {
  visibility: hidden;
  position: fixed;
  transform: translateX(-88%);
  z-index: 999;
  border-radius: 50px 50px 50px 50px;
  padding: 0px 75px 0px 12px;
  background: rgba(37, 44, 72, 0.9);
  height: 50px;
  box-shadow: 0px 0px 2px 1px #4c84ff;
  @media (max-width: 950px) {
    transform: translateX(-75%);
    border-radius: 10px 25px 10px 10px;
    width: 200px;
    height: auto;
    padding: 0 20px 15px;
  }

  .navbar {
    height: 100%;
    @media (max-width: 950px) {
      display: block;
    }

    .navbarLinks {
      display: flex;
      height: 100%;
      @media (max-width: 950px) {
        padding-top: 55px;
        flex-direction: column;
      }

      .navItem {
        padding: 0 10px;
        @media (max-width: 950px) {
          padding: 2px 0;
        }

        &:last-child {
          padding-right: 0;
        }

        .navLink {
          display: flex;
          align-items: center;
          height: 100%;
          text-decoration: none;
          color: #fff;
          font-weight: 600;
          line-height: 26px;
          text-transform: uppercase;
          cursor: pointer;
          @media (max-width: 950px) {
            display: block;
            padding: 8px 0;
          }
        }

        .active {
          color: #4c84ff;
        }
      }
    }
  }
}

.visible {
  visibility: visible;
}






